@import "@styles/reset.scss";
@import url("@styles/fonts.scss");

#root {
  width: 1366px;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media screen and (max-width: 1366px) {
    width: 1024px;
  }

  @media screen and (max-width: 1024px) {
    width: 768px;
  }

  @media screen and (max-width: 768px) {
    width: 320px;
  }
}

input {
  font-family: "Roboto-Normal";
}

p {
  font-family: "Roboto-Normal";
  font-size: 13px;
}

h1 {
  font-family: "Roboto-Normal";
  font-size: 18px;
}

h2 {
  font-family: "Roboto-Normal";
  font-size: 16px;
}
